import * as React from 'react';
import Box from '@mui/material/Box';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import { Link, } from 'react-router-dom';
import {ShoppingOutlined,LeftOutlined,InfoCircleOutlined} from '@ant-design/icons';
import './shoppingdrawer.css'


export default function ShoppingDrawer() {
  const [state, setState] = React.useState({
    
    
    right: false,
  });

  const toggleDrawer = (test, open) => (event) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [test]: open });
  };

  

  const list = (shoppingDrawer) => (
   
    <Box
      className='shoppingDrawer_box'
      role="presentation"
    >
     
          <div className='drawerTitle'>
        <h3 className='drawerTitleName'>
          <Link to='#'   onClick={toggleDrawer(shoppingDrawer, false)}  className='closeIcon'>
          <LeftOutlined />
            </Link>
         购物车</h3>
         <div className='loginIcon'>
         <ShoppingOutlined />
         </div>
          </div>
          <div className='loginMain'>
            <h2 className='loginMainItem'><InfoCircleOutlined /></h2>
            <h3 className='loginMainItem'>正在开发中</h3>
        </div>
    
    </Box >
   
  );

  return (
    <>
      {[ 'right', ].map((anchor) => (
        <React.Fragment key={anchor}>

        <Link to='#' className='User-action-item' onClick={toggleDrawer(anchor, true)}><ShoppingOutlined /></Link>
          
          <SwipeableDrawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
            
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
   </>
  );
}
