import './Header.css';

import {Route,Routes} from 'react-router-dom';

import Develo from './develo.js';
import MasterHeaderHome from './1home/header/master.js';
import HomeSecondNav from './1home/header/second.js';

import MasterHeaderStore from './2store/header/master.js';
import StoreSecondNav1 from './2store/header/second1.js';
import StoreSecondNav2 from './2store/header/second2';

import MasterHeaderExperts from './3experts/header/master.js';
import ExpertsSecondNav1 from './3experts/header/second1';
import ExpertsSecondNav2 from './3experts/header/second2';

import MasterHeaderUsers from './4users/header/master.js';


function OndoApp(){
  return( 
  <div className="App">
    <header className="App-header">
    <MasterHeader/>
    </header>
    <SecondNav/>
   
  </div>
  );
}



function MasterHeader(){
  return(
    <Routes>
      {/* //社区 */}

          <Route path='/*'  element={<MasterHeaderHome/>}></Route>
      {/* //商城 */}

        <Route path='/store/*'  element={<MasterHeaderStore/>}></Route>
      {/* //装修 */}
        <Route path='/experts/*'  element={<MasterHeaderExperts/>}></Route>
      {/* //用户 */}
   
      <Route path='/test/*'  element={<MasterHeaderUsers/>}></Route>
     
     </Routes>
  );
}


function SecondNav(){
  return(
 
    <Routes>
      {/* //社区 */}
        <Route path='/*'  element={<HomeSecondNav/>}></Route>
      {/* //商城 */}
        <Route path='/store'  element={<StoreSecondNav1/>}></Route>
        <Route path='/store/*'  element={<StoreSecondNav2/>}></Route>
       {/* //装修 */}
       <Route path='/experts'  element={<ExpertsSecondNav1/>}></Route>
       <Route path='/experts/*'  element={<ExpertsSecondNav2/>}></Route>
       {/* //user */}
       <Route path='/test/*'  element={<Develo/>}></Route>
      
     </Routes>
     
    
  );
}



  export default OndoApp;