
import {HeartOutlined,CloseOutlined,InfoCircleOutlined } from '@ant-design/icons';
import { Link, } from 'react-router-dom';
import'./messagepopper.css'

import * as React from 'react';
import Popover from '@mui/material/Popover';



export default function MessagePopover() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;

  return (
    <>
         <Link to='#' className='User-action-item' onClick={handleClick}><HeartOutlined /></Link>
     
      <Popover
      
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
       
      >
          <div className='popoverStyle'>
         <div className='popoverTitle'>
        <h3 className='popoverTitleName'><HeartOutlined /> 消息</h3>
          <Link to='#' onClick={handleClose} className='closeIcon'><CloseOutlined /></Link>
          </div>
          <div className='popoverMain'>
            <h2 className='loginMainItem'><InfoCircleOutlined /></h2>
            <h3 className='popoverMainItem'>正在开发中</h3>
        </div>
        </div>
      </Popover>
    </>
  );
}