import { Link, } from 'react-router-dom';
import {UserOutlined,CloseOutlined,InfoCircleOutlined} from '@ant-design/icons';
import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import './loginmodal.css'




export default function LoginModal() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
    <Link to='#' className='User-action-item icon' onClick={handleOpen}><UserOutlined /></Link>
      
      
      <Modal
        open={open}
        onClose={handleClose}

      >
        <Box className='modalstyle'>
          <div className='loginTitle'>
        <h3 className='loginTitleName'><UserOutlined /> 用户登入/注册</h3>
          <Link to='#' onClick={handleClose} className='closeIcon'><CloseOutlined /></Link>
          </div>
          <div className='loginMain'>
            <h2 className='loginMainItem'><InfoCircleOutlined /></h2>
            <h3 className='loginMainItem'>正在开发中</h3>
        </div>
        </Box>
      </Modal>
      </>
  );
}
